tbody tr:nth-child(odd) {
  background-color: #f3f3f3;
}

tbody tr:last-child {
  box-shadow: 0 13px 21px -21px rgba(0, 0, 0, 0.6);
}

.RankListContainer {
  margin: 25px 0;
  border-collapse: collapse;
  font-size: 0.9em;
  width: 100%;
  background-color: #fff;
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.15);
}

.RankListContainer thead tr {
  color: #72be00;
  text-align: left;
}

.RankListContainer th {
  padding: 12px 15px;
}

.RankListItemPlayer {
  display: flex;
  align-items: center;
  height: 80px;
}

.RankListItemPlayer img {
  height: 48px;
  width: 48px;
  margin-right: 16px;
  border-radius: 50%;
}

.RankListItemPlayer span {
  font-weight: bold;
}

td {
  padding: 12px 15px;
  font-size: 16px;
  color: #555;
  cursor: pointer;
}

td:first-of-type {
  width: 0;
  font-size: 28px;
  font-weight: bold;
}

.RankListFooter {
  text-align: center;
}

.RankListButton {
  background-color: #72be00;
  color: white;
  border: none;
  padding: 10px 20px;
  font-size: 16px;
  font-weight: bold;
  cursor: pointer;
  border-radius: 53px;
  box-shadow: inset 0px -4px 4px rgba(0, 0, 0, 0.25);
  transition: background-color 0.2s ease-in-out;
}

.RankListButton:hover {
  background-color: #3e8e41;
}
